import { ShowErrorProps, StatusCode } from '@wix/fe-essentials/error-handler';
import { Translate } from '../../services';

export const commonExportAsyncJobErrors = ({
  translate: t,
}: {
  translate: Translate;
}): Partial<Record<StatusCode, (error: Error | null) => ShowErrorProps>> => ({
  UNAUTHENTICATED: () => ({
    message: t('cairo.export.sessionExpiredMessage'),
    action: {
      text: t('Refresh' as any),
      onClick: () => {
        window.location.reload();
      },
    },
  }),
  PERMISSION_DENIED: () => ({
    message: t('cairo.export.noPermissionsMessage'),
    action: {
      text: t('cairo.export.noPermissionsCTA'),
      onClick: () => {
        window.open(
          'https://support.wix.com/en/article/roles-permissions-overview',
          '_blank',
        );
      },
    },
  }),
});
